* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
    "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #eeeeee;
}
.body {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: calc(100vh - 70px);
}
.nav {
  display: flex;
  justify-content: space-between;
  padding: 10px;
  align-items: center;
  box-shadow: 2px 2px 10px rgba(199, 199, 199, 0.5);
  background-color: white;
}
.nav ul {
  display: flex;
  list-style: none;
}
.nav ul li a {
  display: block;
  padding: 10px 20px;
  text-decoration: none;
}
.form {
  width: 50%;
  margin: 20px auto;
  background-color: #ffffff;
  padding: 20px;
  border-radius: 10px;
}
.form-group {
  margin-bottom: 20px;
}
.form-group input,
.form-group select,
.form-group textarea {
  display: block;
  width: 100%;
  padding: 10px 5px;
  border: 1px solid #dedede;
  border-radius: 5px;
  outline: none;
}
.form-group textarea {
  height: 100px;
}
.form-group button {
  width: 100%;
  display: block;
  border: 1px solid #05054d;
  background: #05054d;
  color: white;
  padding: 12px 5px;
  cursor: pointer;
  outline: none;
  border-radius: 5px;
}
.products-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  width: 90%;
  margin: 20px auto;
  gap: 20px;
}
.grid {
  box-shadow: 2px 2px 10px rgba(199, 199, 199, 0.5);
  padding: 10px;
  border-radius: 10px;
}
.grid img {
  width: 100%;
  height: 300px;
  object-fit: cover;
  object-position: top;
}
